<template>
  <div class="info-div">
    <div class="info-header" style="height: 2em;line-height: 2em">
      <span class="callback" @click="resetComponent">小区列表 </span>
      <span> > 台区线损数据</span>
    </div>
    <div class="all-container" style="height:calc(100% - 1em);margin-top: 1em">
      <div class="operate-div">
        <template v-for="(item, $index) in formList">
          <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right:1.5em"></inputs>
        </template>
      </div>
      <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 5.5em)" >
        <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="openDialogs"></tables>
      </div>
    </div>
    <dialogs type="customer" ref="detail" width="65em" maxHeight="40.5em">
      <div slot="header">
        <div class="head">
          <ul class="deviceTab">
            <li v-for="(item, $index) in tabs"  :class="{'selectTabLi': tabIndex === $index}" @click="getTab($index, item)">
              {{item.title}}
            </li>
          </ul>
          <img class="dialog-img" src="dist/close-icon.png" @click="cancelDialog" title="关闭"/>
        </div>
      </div>
      <div slot="content">
        <div class="LineLoss-content" v-if="detail">
          <div class="loading" v-if="loadings">
              <img src="dist/img/loading.gif">
          </div>
          <div class="operate-div">
            <datePicker ref="timer"  type='doubleDate'
                        :dateOpt="detailList" @getDatePicker="getDeatilFrom" :disabledSDate="TabEndDate" style="width: 20em"></datePicker>
          </div>
          <div class="mychar-box" v-show="tabIndex==0">
            <div ref="mychar" class="mychar">
                <div style="width: 100%;text-align: center;font-size: 0.8em">
                    <img src="/dist/img/empty-data.png" alt="">
                    <p>查不到相应的数据</p>
                </div>
            </div>
          </div>
          <div ref="myOwnTable2" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:21.5em;margin-top: 0" v-show="tabIndex===0">
            <tables ref="myTable2" :tableOpt="tablequeryDay" class="tablePadding"></tables>
          </div>
          <div ref="myOwnTable3" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 4.3em)" v-if="tabIndex!=0">
            <tables ref="myTable3" :tableOpt="tableSupply" class="tablePadding"></tables>
          </div>
        </div>
      </div>
    </dialogs>
  </div>
</template>
<script>
  export default{
    data() {
      return {
        tableData:[],
        searchParam: {
          startDate: '',
          endDate: '',
          platform: '',
          lossType: '',
          queryGuid: '',
          residential: '',
          orgNo: ''
        },
        queryGuid: "",
        tableShow:false,
        loadings: true,
        tableOpt: {
          column: [
            {title: '台区名称', field: 'platform',textAlign: 'center',width: '250'},
            {title: '电表数量', field: 'electDeviceAmount', textAlign: 'center', width: '160'},
            {title: '供电量（KW·h）', field: 'powerSupply', textAlign: 'center', width: '160'},
            {title: '售电量（KW·h）', field: 'electSale', textAlign: 'center', width: '160'},
            {title: '损耗电量（KW·h）', field: 'electLoss', textAlign: 'center', width: '160'},
            {title: '损耗率（%）', field: 'occupation', textAlign: 'center', width: '60'}
          ],
          url: 'lineLoss/queryLineLostList',
          method: 'post',
          // method: 'get',
          // url: 'dist/json/linelossData.json',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {},
          isExport: true,
          exportURL: 'lineLoss/queryLineLostListDownload',
        },
        detailParam:{
          startDate: "",
          endDate: "",
          platform: "",
          meterCno: ""
        },
        // taburl: 'dist/json/queryDayLost.json',
        taburl: 'lineLoss/queryDayLost',
        // taburl: 'lineLoss/queryDayL',
        detail: false,
        tabIndex: 0,
        typeChars: "",
        TabEndDate: "",
        platform: "",
        tabDates:[{start:"",end:""},{start:"",end:""},{start:"",end:""},{start:"",end:""}],
        tabs:[{title: "日损耗", url:""},{title: "供入电量明细", url:""},{title: "供出电量明细", url:""},{title: "日抄表成功率", url:""}],
        // 日损耗
        tablequeryDay:{
          column: [],
          shareTable: true,
          // url: 'lineLoss/queryDayLost',
          // method: 'post',
          // method: 'get',
          // url: 'dist/json/queryDayLost.json',
          pageSize: 9999999,
          pageNumber: 1,
          // pageList: false,
          queryParam: {},
          isExport: true,
          noExportCurrent: true,
          noPagination: true,
          exportURL: 'lineLoss/queryDayLostDownload',
        },
        tableSupply:{
          pageSize: 20,
          pageNumber: 1,
          shareTable: true,
          pageList: [20, 40, 60],
          queryParam: {},
          isExport: true,
         // data: []
        }
      }
    },
    props: ['row'],
    computed: {
      detailList: function () {
        let dates = this.tabDates[this.tabIndex];
        return {
          key: 'searchDate',
          value: {startDate: dates.start, endDate: dates.end},
          width: '20em',
          fontSize: '0.8em'
        }
      },
      formList: function () {
        let dates = this.initDate(true);
        return [
          {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              selectOpt: {
                width: '7em',
                cs: 'client',
                data: [
                  {text: '台区名称', value: 'platform'},
                ]
              }
            }
          }
        ]
      },
      columns: function () {
        // 供入
        let tableDayCost = {
          column: [
            {title: '序号', field: 'Index',textAlign: 'center',width: '30'},
            {title: '日期', field: 'day',textAlign: 'center',width: '100'},
            {title: '供电量(KW·h)', field: 'powerSupply', textAlign: 'center', width: '100'},
            {title: '售电量(KW·h)', field: 'electSale', textAlign: 'center', width: '100'},
            {title: '损耗电量(KW·h)', field: 'electLoss', textAlign: 'center', width: '100'},
            {title: '损耗率(%)', field: 'lossRate', textAlign: 'center', width: '80'},
            {title: '成功数', field: 'succeedAmount', textAlign: 'center', width: '100'},
            {title: '成功率(%)', field: 'succeedRate', textAlign: 'center', width: '80'},
          ],
          url: '',
          data: this.tableData,
          exportURL: 'lineLoss/queryDayLostDownload',
        }
        let tableSupply = {
          column: [
            {title: '日期', field: 'calcDate',textAlign: 'center',width: '100'},
            {title: '用户名称', field: 'customerName', textAlign: 'center', width: '200'},
            {title: '表号', field: 'electDeviceNo', textAlign: 'center', width: '80'},
            {title: '倍率', field: 'ratio', textAlign: 'center', width: '50'},
            {title: '电量（KW·h）', field: 'elect', textAlign: 'center', width: '100'},
            {title: '起表码', field: 'startCode', textAlign: 'center', width: '80'},
            {title: '止表码', field: 'endCode', textAlign: 'center', width: '80'},
            {title: '采集时间', field: 'collectTime', textAlign: 'center', width: '130'},
          ],
            url: 'lineLoss/querySupplyElectDetail',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/querySupplyElectDetail.json',
            // noExportCurrent: true,
            // noPagination: true,
            exportURL: 'lineLoss/querySupplyElectDetailDownload',
        };
        // 供出
        let tableQueryConfess = {
          column: [
            {title: '日期', field: 'calcDate',textAlign: 'center',width: '130'},
            {title: '用户名称', field: 'customerName', textAlign: 'center', width: '200'},
            {title: '门牌号', field: 'propertyName', textAlign: 'center', width: '80'},
            {title: '表号', field: 'electDeviceNo', textAlign: 'center', width: '80'},
            {title: '倍率', field: 'ratio', textAlign: 'center', width: '50'},
            {title: '电量(KW·h)', field: 'elect', textAlign: 'center', width: '100'},
            {title: '起表码', field: 'startCode', textAlign: 'center', width: '50'},
            {title: '止表码', field: 'endCode', textAlign: 'center', width: '50'},
            {title: '采集时间', field: 'collectTime', textAlign: 'center', width: '130'},
          ],
            url: 'lineLoss/queryConfessElectDetail',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/queryConfessElectDetail.json',
            exportURL: 'lineLoss/queryConfessElectDetailDownload',
        }
        // 日抄表
        let tableDay = {
          column: [
            {title: '序号', field: 'Index',textAlign: 'center',width: '30'},
            {title: '日期', field: 'day', textAlign: 'center', width: '150'},
            {title: '电表总数', field: 'electDeviceAmount', textAlign: 'center', width: '100'},
            {title: '成功数', field: 'succeedAmount', textAlign: 'center', width: '100'},
            {title: '失败数', field: 'failAmount', textAlign: 'center', width: '100'},
            {title: '成功率(%)', field: 'succeedRate', textAlign: 'center', width: '100'},
          ],
            url: 'lineLoss/queryDayCollectSucceedRate',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/queryDayCollectSucceedRate.json',
            exportURL: 'lineLoss/queryDayCollectSucceedRateDownload',
        }
        return [tableSupply, tableQueryConfess, tableDay, tableDayCost]
      }
    },
    methods: {
      openDialogs: function (val) {
        this.detail = true;
        this.$refs.detail.show();
        let platform = val.val.platform;
        this.platform = platform;
        this.tablequeryDay.queryParam['platform'] = this.detailParam.platform = platform;
        this.tablequeryDay.queryParam['startDate'] = this.detailParam.startDate = this.tabDates[0].start;
        this.tablequeryDay.queryParam['endDate'] = this.detailParam.endDate = this.tabDates[0].end;
        this.tablequeryDay.queryParam['meterCno'] = this.detailParam.meterCno = val.val.meterCno;
        this.getDetailData();
      },
      getDeatilFrom:function (val) {
        let arr = val.val.split("至");
        if(this.tabIndex === 0){
          this.detailParam.startDate = arr[0];
          this.detailParam.endDate = arr[1];
          this.getDetailData();
        }else{
          let myTable = 'myTable3'
          this.tabDates[this.tabIndex].start = arr[0];
          this.tabDates[this.tabIndex].end = arr[1];
          let param = this.detailParam;
          param.startDate = arr[0]
          param.endDate = arr[1];
          this.$refs[myTable].refreshTable(param, 1);
        }
      },
      tabSize: function(){
        let self = this;
        let tabIndex = this.tabIndex === 0 ? 2:3;
        let myTable = 'myTable' + tabIndex
        let myOwnTable1 = 'myOwnTable' + tabIndex
        this.$nextTick(function () {
          self.$refs[myTable].onResize(false, self.$refs[myOwnTable1]);
        })
      },
      getDetailData:function(){
        let self = this;
        let param = self.detailParam;
        param.pageNumber = 1;
        param.pageSize = 999999;
        self.getRequest({
          url: self.taburl,
          method: 'post',
          param: param,
          success: function (data) {
              self.loadings = false;
              self.tableData = data.data;
              self.$refs.myTable2.onResize(false, self.$refs.myOwnTable2);
              self.$refs.myTable2.resetTableOpt(self.columns[3]);
              if(self.tabIndex === 0){
                self.setChars();
              }
          },
          error: function (){
              self.loadings = false;
              self.$refs.myTable2.resetTableOpt({data: []});
          }
        });
      },
      setChars: function () {
        var self = this;
        var option = {
          tooltip : {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          legend: {
            data:['损耗率','采集成功率'],
            left: "5%",
            top: "5%",
            borderRadius: 11,
            icon: 'roundRect',
            itemGap:50,
            itemWidth: 35,
            itemHeight: 8,
            textStyle : {
              color: 'rgba(255,255,255, 0.9)'
            }
          },
          grid: {
            left: '3%',
            right: '0%',
            bottom: '5%',
            top: '25%',
            containLabel: true
          },
          xAxis : [
            {
              type : 'category',
              boundaryGap : [1,1],
              data : [],
              axisLine:{
                show: true,
                lineStyle:{
                  color: '#fff'
                }
              },
              axisLabel:{
                showMinLabel: true,
              },
              axisTick:{
                show: false
              },
            }
          ],
          yAxis : [
            {
              type : 'value',
              axisLine: {
                show: false,
                lineStyle:{
                  color: '#3C9BF9'
                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: true,
                lineStyle: {
                  color: ['#20314F']
                }
              },
              axisLabel: {
                show: true,
                formatter:'{value}',
                textStyle: {
                  color: '#fff'
                }
              },
            }
          ],
          series : [
            {
              name:'损耗率',
              type:'line',
              stack: '损耗率',
              symbol: 'emptyCircle',
              lineStyle:{
                color: '#EB9B3C'
              },
              itemStyle: {
                color: '#EB9B3C',
                borderWidth: 3
              },
              data:[]
            },
            {
              lineStyle:{
                color: '#217FDF'
              },
              itemStyle: {
                color: '#217FDF',
                borderWidth: 3
              },
              name:'采集成功率',
              type:'line',
              stack: '采集成功率',
              symbol: 'emptyCircle',
              data:[]
            },
          ]
        };
        for (let i = 0; i < self.tableData.length; i++){
          option.xAxis[0].data.push(self.tableData[i].day);
          option.series[0].data.push(self.tableData[i].lossRate);
          option.series[1].data.push(self.tableData[i].succeedRate);
        }
        this.typeChars = echarts.init(self.$refs.mychar);
        this.typeChars.setOption(option);
      },
      getTab: function (index, item) {
        let self = this;
        if(index === this.tabIndex){
          return;
        }
        this.tabIndex = index;
        this.tableSupply.url = "";
        let colIndex = index - 1;
        if(index !=0 ){
          self.loadings = false;
          setTimeout(function () {
            let tabObj = self.extendObj(self.tableSupply, self.columns[colIndex]);
            tabObj.queryParam['platform'] = self.platform;
            tabObj.queryParam['startDate'] = self.tabDates[index].start;
            tabObj.queryParam['endDate'] = self.tabDates[index].end;
            tabObj.queryParam['meterCno'] = self.detailParam.meterCno;
            self.tabSize()
            self.$refs.myTable3.resetTableOpt(tabObj);
          },0)
        }else{
          self.loadings = true;
          this.getDetailData();
        }
      },
      cancelDialog: function () {
        this.$refs.detail.close();
        this.detail = false;
        this.tabIndex = 0;
        this.setTabDates();
        this.loadings = true;
      },
      getParams: function (pageNumber) {
        let arr = this.getSearchData(this.searchParam);
        arr.platform = this.searchParam.platform
        this.getHeight('calc(100% - 3.5em)');
        this.searchTable(arr, pageNumber);
      },
      getFrom: function (val) {
        if (val.combine) {
          if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
            this.getOption(val, true);
            this.getParams(1);
          }
        }
      },
      windowChange: function(){
        let that = this;
        this.getHeight();
        if(that.detail){
            that.typeChars.resize()
            that.tabSize();
        }
        // window.onresize = function () {
        //   that.getHeight();
        //   if(that.detail){
        //     that.typeChars.resize()
        //     that.tabSize();
        //   }
        // }
      },
      getDate: function(){
        let data = new Date();
        let sMonths, eMonths, start, end;
        let yearNow = data.getFullYear();
        let monthNow = data.getMonth();
        let dateNow = data.getDate();
        let dataNew = new Date();
        dataNew.setMonth(dataNew.getMonth() - 2);
        let day = dateNow > 9 ? dateNow : '0' + dateNow;
        let dateNew = dataNew.getDate();
        let days = dateNew > 9 ? dateNew : '0' + dateNew;
        let monthNew = dataNew.getMonth();
        start = dataNew.getFullYear() + '-' + ((monthNew) < 10 ? ('0' + (monthNew + 1)) : (monthNew + 1)) + '-' + days;
        end = yearNow + '-' + ((monthNow + 1) < 10 ? ('0' + (monthNow + 1)) : (monthNow + 1)) + '-' + day;
        return {
          start: start,
          end: end
        }
      },
      resetComponent:function () {
        this.$emit('closeDiv');
      },
      setTabDates: function () {
          let dates = this.initDate(true);
          for(let i = 0; i < this.tabDates.length; i++){
              this.tabDates[i].start = dates.start;
              this.tabDates[i].end = dates.end;
          }
      }
    },
    mounted: function() {
      this.windowChange()
    },
    created: function () {
      var self = this;
      this.queryGuid = this.searchParam.queryGuid = this.row.queryGuid;
      this.tableOpt.queryParam['startDate'] = this.searchParam.startDate = this.row.startDate;
      this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = this.row.endDate;
      this.tableOpt.queryParam['platform'] = this.searchParam.platform;
      this.tableOpt.queryParam['lossType'] = this.searchParam.lossType = this.row.lossType;
      this.tableOpt.queryParam['queryGuid'] = this.queryGuid;
      this.tableOpt.queryParam['residential'] = this.searchParam.residential = this.row.residential;
      this.tableOpt.queryParam['orgNo'] = this.searchParam.orgNo = this.row.orgNo;
      setTimeout(function () {
        self.tableShow = true;
      },0)
      let dateTab = this.getDate();
      this.TabEndDate = dateTab.start;
      this.setTabDates();
    }
  }
</script>
<style lang="scss">
  .dialog-img{
    float: right;
    cursor: pointer;
  }
  .head{
    margin-bottom: -1.2em;
    overflow: hidden;
    ul.deviceTab{
      float: left;
      text-align: center;
      li{
        width: 8.4em;
        padding: 0.7em 0;
        float: left;
        cursor: pointer;
        position: relative;
      }
      li:first-child:before, li:hover:before, li.selectTabLi:before, li:hover + li:before,  li.selectTabLi +  li:before, li.rightBtnLi:before{
        width:0
      }
      li:before{
        content: '';
        display: block;
        position: absolute;
        background-color: rgba(255, 255, 255, 0.6);
        width: 2px;
        height: 80%;
        left: 0;
        top: 10%;
      }
      li:hover, .selectTabLi{
        background: #0276D9;
      }
    }
  }
  @media screen and (max-width: 1366px) {
    .LineLoss-content ,.mychar{
      font-size: 12px;
    }
  }
  @media screen and (min-width: 1367px) and (max-width: 1700px) {
    .LineLoss-content,.mychar {
      font-size: 14px;
    }
  }

  @media screen and (min-width: 1701px) and (max-width: 1920px) {
    .LineLoss-content,.mychar {
      font-size: 17px;
    }
  }
  .LineLoss-content{
    overflow: hidden;
    width: 62em;
    height: 35em;
    position: relative;
    .operate-div{
      position: absolute;
      top: -8px;
      right: 20px;
      z-index: 10;
    }
    .mychar-box{
      height: 15em;
      width: 100%;
      position: relative;
      .mychar{
        height: 15em;
        width: 60em;
        position: relative;
      }
    }
    .table-container{
      margin-top: 4em;
    }
    .loading{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: calc(100% - 2px);
        background: rgba(2, 22, 69, 1);
        z-index: 10;
        img{
            width: 4em;
            height: 4em;
            position: absolute;
            top: calc(50% - 2em);
            left: calc(50% - 2em);
        }
    }
  }
</style>



