<template>
  <div ref="trys" style="height: 100%">
    <div v-if="showInfo" class="info-container">
      <info :row="row" @closeDiv="closeDiv" ref="infoDetail"></info>
    </div>
    <div  class="all-container" style="height:100%;margin-top:0" v-show="!showInfo">
      <div class="operate-div">
        <template v-for="(item, $index) in formList">
          <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right:1.5em"></inputs>
          <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                      @getDatePicker="getFrom"></datePicker>
        </template>
      </div>
      <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 4.5em)" >
        <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"></tables>
      </div>
    </div>
    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
  import info from './LineLossData.vue'
  import {GUID} from '../../assets/GUID.js'
  export default{
    data() {
      return {
        row: {},
        showInfo: false,
        searchParam: {
          startDate: '',
          endDate: '',
          residential: '',
          queryGuid: "",
        },
        controls: [],
        tableShow:false,
        queryGuid: "",
        tableOpt: {
          column: [
            {title: '序号', field: 'Index',textAlign: 'center',width: '5%'},
            {title: '小区名称', field: 'residential',textAlign: 'center',width: '20%'},
            {title: '台区数', field: 'platformTotal', textAlign: 'center', width: '15%'},
            {title: '台区数', field: 'lineLossRateLess0platform', textAlign: 'center', width: '10%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index,row);
                    }
                  }
                }

              }},
            {title: '占比(%)', field: 'lineLossRateLess0occupation', textAlign: 'center', width: '10%'},
            {title: '台区数', field: 'lineLossRateLess10platform', textAlign: 'center', width: '10%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index,row);
                    }
                  }
                }

              }},
            {title: '占比(%)', field: 'lineLossRateLess10occupation', textAlign: 'center', width: '10%'},
            {title: '台区数', field: 'lineLossRateBigger10platform', textAlign: 'center', width: '10%',type: "object",
              formatter: function (val, index, row, obj) {
                let template;
                template = '<span class="list-operate"  @click="openAccount" style="color:#C68F3C">' + val +'</span>';
                return {
                  template: template,
                  methods: {
                    openAccount: function () {
                      obj.$parent.openDialogs(index,row);
                    }
                  }
                }

              }
            },
            {title: '占比(%)', field: 'lineLossRateBigger10occupation', textAlign: 'center', width: '10%'},
          ],
          url: 'lineLoss/queryResidentialList',
          method: 'post',
          // method: 'get',
          // url: 'dist/json/lineloss.json',
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {},
          isExport: true,
          exportURL: 'lineLoss/queryResidentialListDownload',
          istotalhead: true,
          totalhead:[
            {title: '线损率<0%', totalFieldield: ['lineLossRateLess0platform', 'lineLossRateLess0occupation'], field:"lineLossRateLess0occupation",textAlign: 'center', width: '20%'},
            {title: '0%<=线损率<=10%', totalFieldield: ['lineLossRateLess10platform', 'lineLossRateLess10occupation'], field:"lineLossRateLess10occupation",textAlign: 'center', width: '20%'},
            {title: '线损率>10%', totalFieldield: ['lineLossRateBigger10platform','lineLossRateBigger10occupation'], field:"lineLossRateBigger10occupation",textAlign: 'center', width: '20%'},
          ]
        },
      }
    },
    components: {
      info
    },
    computed: {
      formList: function () {
        let dates = this.initDate(true);
        return [
          {
            type: 'timer',
            options: {
              key: 'searchDate',
              value: {startDate: dates.start, endDate: dates.end},
              width: '23em',
              fontSize: '0.8em'
            }
          },
          {
            type: 'input',
            options: {
              type: 'typeSearch',
              key: 'searchParam',
              selectOpt: {
                width: '7em',
                cs: 'client',
                data: [
                  {text: '小区名称', value: 'residential'}
                ]
              }
            }
          }
        ]
      }
    },
    methods: {
      closeDiv:function () {
        this.showInfo = false;
        this.getParams();
      },
      openDialogs:function (index,row) {
        let self = this;
        let lossData = this.tableOpt.totalhead;
        let lossType = 0;
        for(let i = 0; i < lossData.length; i++){
          if(lossData[i].totalFieldield.indexOf(index) !== -1){
            console.log(i);
            switch (i){
              case 0:
                lossType = 0;
                break;
              case 1:
                lossType = 10;
                break;
              case 2:
                lossType = 11;
                break;
            }
          }
        }
        this.row = {
          queryGuid:self.searchParam.queryGuid,
          lossType:lossType,
          startDate: self.searchParam.startDate,
          endDate: self.searchParam.endDate,
          residential:row.residential,
          orgNo: row.orgNo || ''
        }
        this.showInfo = true;
      },
      getParams: function (pageNumber) {
        let arr = this.getSearchData(this.searchParam);
        arr.residential = this.searchParam.residential
        this.getHeight('calc(100% - 3.5em)');
        this.searchTable(arr, pageNumber);
      },
      getFrom: function (val) {
        if (val.combine) {
          if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
            this.searchParam = this.cleanSearchData(this.searchParam, ['startDate', 'endDate', 'queryGuid','residential']);
            this.getOption(val, true);
            let guid = new GUID().newGUID();
            this.queryGuid = this.searchParam.queryGuid = guid.split("-").join("");
            this.getParams(1);
          }
        } else {
          this.getOption(val, true);
          if(val.key == 'searchDate')  {
            let arr = val.val.split('至');
            this.searchParam.startDate = arr[0];
            this.searchParam.endDate = arr[1];
            let guid = new GUID().newGUID();
            this.queryGuid = this.searchParam.queryGuid = guid.split("-").join("");
          }
          this.getParams(1);
        }
      },
      windowChange: function(){
        let that = this;
        this.getHeight();
        // window.onresize = function () {
        //   that.getHeight();
        // }
      },
      init: function () {
          this.tableOpt.isExport = this.controls[6];
          this.tableShow = true;
          this.windowChange()
      }
    },
    mounted: function() {
      //this.windowChange()
    },
    created: function () {
      this.getAction();
      let dates = this.initDate(true);
      let guid = new GUID().newGUID();
      this.queryGuid = this.searchParam.queryGuid = guid.split("-").join("");
      this.tableOpt.queryParam['startDate'] = this.searchParam.startDate = dates.start;
      this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = dates.end;
      this.tableOpt.queryParam['residential'] = this.searchParam.residential;
      this.tableOpt.queryParam['queryGuid'] = this.queryGuid;
    }
  }
</script>
<style lang="scss" scoped>
</style>



